@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Open Sans Condensed', sans-serif;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  letter-spacing: 2px;
}
button {
    border-radius: 38px;
    padding: 15px 32px;
    text-align: center;
    font-size: 16px;
    margin-top: 20px;
    border: none;
    background-color: #000;
    color: #fff;
    opacity: 0.75;
  }

  button:hover {
    opacity: 1;
    cursor: pointer;
  }
#app {
    opacity: 0.5;
}


